<template>
  <div>
    <loading :active.sync="isLoading"></loading>

    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openCouponModal(true)">
        
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in coupons" :key="key">
          <td>{{ item.title }}</td>
          <td>{{ item.percent }}%</td>
          <td>{{ $moment.unix(item.due_date).format('YYYY-MM-DD') }}</td>
          <td>
            <span v-if="item.is_enabled === 1" class="text-success"></span>
            <span v-else class="text-muted"></span>
          </td>
          <td>
            <div class="btn-group">
              <button class="btn btn-outline-primary btn-sm"
                @click="openCouponModal(false, item)"
              ></button>
              <button class="btn btn-outline-danger btn-sm"
                @click="openDelModal(item)"></button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Pagination -->
    <Pagination :pages="pagination" @emitPages="getCoupons"></Pagination>

    <!-- Modal -->
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title"></label>
              <input type="text" class="form-control" id="title" v-model="tempCoupon.title"
                placeholder="">
            </div>
            <div class="form-group">
              <label for="coupon_code"></label>
              <input type="text" class="form-control" id="coupon_code" v-model="tempCoupon.code"
                placeholder="">
            </div>
            <div class="form-group">
              <label for="due_date"></label>
              <input type="date" class="form-control" id="due_date"
                v-model="due_date">
            </div>
            <div class="form-group">
              <label for="price"></label>
              <input type="number" class="form-control" id="price"
                v-model="tempCoupon.percent" placeholder="">
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox"
                  :true-value="1"
                  :false-value="0"
                  v-model="tempCoupon.is_enabled" id="is_enabled">
                <label class="form-check-label" for="is_enabled">
                  
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" class="btn btn-primary"
              @click="updateCoupon"></button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempCoupon.title }}
            ({{ tempCoupon.code }})</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger"
              @click="delCoupon"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import Pagination from '../Pagination';

export default {
  data() {
    return {
      pagination: {},
      isLoading: false,
      coupons: {},
      tempCoupon: {
        title: '',
        is_enabled: 0,
        percent: 100,
        due_date: 0,
        code: '',
      },
      due_date: this.$moment().format('YYYY-MM-DD'),
      isNew: false,
    };
  },
  watch: {
    due_date() {
      const vm = this;
      vm.tempCoupon.due_date = vm.$moment(vm.due_date).format('X');
    },
  },
  components: {
    Pagination,
  },
  methods: {
    openCouponModal(isNew, item) {
      const vm = this;
      $('#couponModal').modal('show');
      vm.isNew = isNew;
      if (vm.isNew) {
        vm.tempCoupon = {};
        vm.due_date = vm.$moment().format('YYYY-MM-DD');
      } else {
        vm.tempCoupon = Object.assign({}, item);
        // 
        vm.due_date = vm.$moment(vm.tempCoupon.due_date * 1000).format('YYYY-MM-DD');
      }
    },
    openDelModal(item) {
      this.tempCoupon = Object.assign({}, item);
      $('#delProductModal').modal('show');
    },
    getCoupons(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((res) => {
        console.log(res.data);
        vm.isLoading = false;
        vm.coupons = res.data.coupons;
        vm.pagination = res.data.pagination;
      });
    },
    updateCoupon() {
      const vm = this;
      if (vm.isNew) {
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
        this.$http.post(api, { data: vm.tempCoupon }).then((res) => {
          console.log(res.data, vm.tempCoupon);
          $('#couponModal').modal('hide');
          this.getCoupons();
        });
      } else {
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        this.$http.put(api, { data: vm.tempCoupon }).then((res) => {
          console.log(res.data, vm.tempCoupon);
          $('#couponModal').modal('hide');
          this.getCoupons();
        });
      }
    },
    delCoupon() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      this.$http.delete(api).then((res) => {
        console.log(res.data);
        if (res.data.success) {
          $('#delProductModal').modal('hide');
          vm.getCoupons();
        } else {
          $('#delProductModal').modal('hide');
          vm.getCoupons();
          console.log('');
        }
      });
    },
  },
  created() {
    this.getCoupons();
  },
};
</script>



